
import { defineComponent } from 'vue'
import { useSignupEffect } from './hooks/useSignupEffect'

export default defineComponent({
  name: 'Signup',
  setup() {
    const {
      username,
      password,
      confirmPassword,
      captchaSvg,
      captcha,
      requestCaptcha,
      onSubmit
    } = useSignupEffect()

    requestCaptcha()

    return {
      onSubmit,
      username,
      password,
      confirmPassword,
      captcha,
      captchaSvg
    }
  }
})
